.slider-container {
    position: relative;
    width: 100%;
    overflow: hidden;
    margin-left: 50px;
    margin-right: 50px;
    margin-bottom: 50px;
    padding-bottom: 50px;
}

.slider-content {
    display: flex;
    transition: transform 0.5s ease;
    position: relative;
}

.rectangle {
    flex: 0 0 calc(100%/3);
    height: 120px; 
    background-color: #77777739;
    border-radius: 20px;
    margin-right: 10px;
    font-size: 1.25em;
    text-align: center;
}
.link{
    display: block;
    position: relative;
    top: 30%;
    background-color: transparent;
    text-decoration:underline;
}

.arrow {
    position: absolute;
    top: 25%;
    background-color: yellow;
    border-radius: 50%;
    cursor: pointer;
    font-size: 1.25em;
    color:black;
    padding: 5px;
    z-index: 3;
    width: 30px;

}

.arrow.next {
    right: 0px;
}

.arrow.prev {
    left: 0px;
}

.dots {
    position: absolute;
    bottom:0px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
}

.dot {
    height: 15px;
    width: 15px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
    margin: 0 5px;
    cursor: pointer;
}

.active {
    background-color:rgba(0, 0, 0, 0.406);
    cursor: default;
}

.overlay_icon_carousel {
    width: 30px;
    height: 30px;
    background-color: transparent;
    border-radius: 50%;
    border:1px solid;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: relative;
    top:45px;
    left:45%;
}

.overlay_icon_prompt span{
    font-size: 20px;
    background-color: transparent;
}

.overlay-container {
    position: relative;
    top:50px;
}